@import '~include-media/dist/include-media';

.TodoTemplate {
  width: 512px;
  margin: 100px auto;
  @include media('<=1239px') {
    margin: 0px auto 50px;
  }
  border-radius: 4px;
  .app-title {
    background: #22b8cf;
    color: white;
    height: 4rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .content {
    background: white;
  }
}
